import { createHelpers } from 'vuex-map-fields';
import MyFilter from './Filter.vue';

const { mapFields } = createHelpers({
  getterType: 'pins/getField',
  mutationType: 'pins/updateField',
});

export default {
  name: 'MenuComponent',
  components: {
    MyFilter,
  },
  emits: ['call-filter'],
  data() {
    return {
      barStyle: {
        right: "0",
        borderRadius: "4px",
        backgroundColor: "#eee",
        width: "4px",
        opacity: .8,
      },
    };
  },
  computed: {
    ...mapFields(['expand']),
    listEvents() {
      return this.$store.getters['events/getRecents'];
    },
    listTopics() {
      return this.$store.getters['topics/getRecents'];
    },
  },
  methods: {
    forwardCall(el) {
      this.$emit('call-filter', el);
    },
    async openEvent(eventId) {
      this.$router.push({
        name: "Agenda",
        hash: `#${eventId}`
      });
    },
  },
};
